@import "src/styles-global/mixins"

.page__terms
  position: relative
  &__wrap
    padding-top: 2%
    padding-left: 4.2%
    padding-right: 4.2%
    display: flex
    width: 100%
    flex-direction: column
  &__content
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  &__header
    width: 100%
    display: flex
    justify-content: flex-end
    color: #000
    text-align: right
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__lineImg
    margin-top: 1%
    margin-bottom: 1%
    width: 100%
  &__logoImg
    width: 10%
    min-width: 0
  &__backButton
    padding: 15px 20px
    left: 3%
    top: 1%
    position: sticky
    position: -webkit-sticky
    cursor: pointer
    width: 102px
    font-family: Inter, sans-serif
    background: #4c5665
    border-radius: 50px
    border: 1px solid #a8a7a7
  &__backButton:hover
    opacity: 0.9
  &__textButton
    color: white
    font-size: 20px


@media screen and (max-width: 1600px)
  .page__terms__header
    font-size: 36px
  .page__terms__logoImg
    width: 12%
@media screen and (max-width: 1300px)
  .page__terms__header
    font-size: 32px
  .page__terms__logoImg
    width: 14%
@media screen and (max-width: 1000px)
  .page__terms__header
    font-size: 28px
  .page__terms__logoImg
    width: 16%
@media screen and (max-width: 700px)
  .page__terms__lineImg
    margin-top: 30px
    margin-bottom: 30px
  .page__terms__logoImg
    margin-bottom: 30px
  .page__terms__logoImg
    width: 18%
  .page__terms__backButton

