@import "src/styles-global/mixins"

.section__catalog__buttonRow>button
  box-sizing: border-box
  width: auto
  height: auto
  border-radius: 20px
  border: 2px solid #000
  background: #FFF
  padding: 0.57% 4.549%
  display: flex
  justify-content: center
  align-items: center
  font-size: 24px
  font-weight: 400
  font-family: Inter, sans-serif

.section__catalog__buttonRowHidden1>button,
.section__catalog__buttonRowHidden2>button
  box-sizing: border-box
  width: auto
  height: auto
  border-radius: 20px
  border: 2px solid #000
  background: #FFF
  padding: 0.57% 3%
  display: flex
  justify-content: center
  align-items: center
  font-size: 16px
  font-weight: 400
  font-family: Inter, sans-serif

@media screen and (max-width: 461px)
  .section__catalog__buttonRowHidden1>button,
  .section__catalog__buttonRowHidden2>button
    border: 1px solid #000000 !important

@media screen and (max-width: 1100px)
  .section__catalog__buttonRow>button
    padding: 0.57% 3.549%
    font-size: 20px

.section__catalog__buttonRow>button:hover
  border: 2px solid #0047FF !important

.section
  &__catalog
    display: flex
    flex-direction: column
    margin-top: 4.2%
    padding-left: 4.2%
    padding-right: 4.2%
  &__catalog__items2,
  &__catalog__items3,
  &__catalog__items4,
  &__catalog__items5
    display: none

  &__catalog__header
    color: #000
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
    display: block
  &__catalog__line
    width: 100%
    margin-bottom: 2.275%
    margin-top: 2.275%
  &__catalog__buttonRow
    flex-wrap: wrap
    box-sizing: border-box
    width: auto
    height: auto
    padding-left: 3%
    padding-right: 3%
    display: flex
    flex-direction: row
    align-items: center
    justify-content: space-between
  &__catalog__buttonRedirect
    display: flex
    align-items: center
    justify-content: center
    margin-left: 3%
    border-radius: 30px
    background: #3D4350
    padding: 0.8% 1.43%
  &__catalog__buttonRedirect:hover
    opacity: 0.9
  &__catalog__buttonRedirectText
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__catalog__textRight
    display: flex
    align-items: flex-end
    justify-content: flex-end
    color: #000
    font-family: Inter, sans-serif
    font-size: 32px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__catalog__textLeft
    display: flex
    align-items: flex-start
    color: #000
    font-family: Inter, sans-serif
    font-size: 32px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__catalog__buttonBlock
    margin-top: 4.6%
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
  &__catalog__imgSmallLine
    margin-top: 3%
    width: 100%

.fake__dynamic__slider
  margin-top: 2.843%
  width: 100%
  display: flex
  justify-content: space-between
  align-items: center
.section__catalog__items
  background-color: white
  display: flex
  justify-content: space-between
  flex-direction: column
  width: 90%
  margin: 5%
  height: 40rem
  border-radius: 0px
  border: 1px solid #a4a3a3
  overflow: auto
.section__catalog__itemsRow
  margin-top: 5%
.section__catalog__itemsRow
  display: flex
  flex-direction: row
  justify-content: space-around
.fakeSlider__elem
  &__ender
    border-radius: 30px
    width: 13%
    height: 15rem
    background: #91949d
  &__inner
    border-radius: 30px
    width: 14%
    height: 20rem
    background: #91949d
  &__center
    border-radius: 30px
    width: 28%
    height: 37rem
    background: #91949d

.section__catalog__textBlock
  width: 35%
  display: flex
  align-items: flex-end
  justify-content: flex-end
  flex-direction: column
.section__catalog__imgSmallLine2
  width: 100%
.section__catalog__textBlock2
  margin-top: 4%
  display: flex
  width: 35%
  align-items: flex-start
  flex-direction: column
.section__catalog__buttonRowHidden1
  padding: 0 10%
  display: flex
  justify-content: space-between
  flex-direction: row
.section__catalog__buttonRowHidden2
  display: flex
  margin-top: 3%
  padding: 0 10%
  justify-content: center
  flex-direction: row
.section__catalog__buttonRowHiddenWrap
  width: 100%
  justify-content: center
  display: none
  flex-direction: column
.section__catalog__buttonRowHiddenWrap2
  width: 100%
  justify-content: center
  display: none
  flex-direction: column
.section__catalog__imgSmallLine2
  width: 100%
  margin-bottom: 3%





@media screen and (max-width: 1700px)
  .section__catalog__buttonRedirectText
    font-size: 22px
  .section__catalog__textRight, .section__catalog__textLeft
    font-size: 29px
@media screen and (max-width: 1500px)
  .section__catalog__buttonRedirectText
    font-size: 20px
  .section__catalog__textRight, .section__catalog__textLeft
    font-size: 25px
@media screen and (max-width: 1300px)
  .section__catalog__buttonRedirectText
    font-size: 18px
  .section__catalog__textRight, .section__catalog__textLeft
    font-size: 24px
@media screen and (max-width: 1100px)
  .section__catalog__buttonRedirectText
    font-size: 16px
  .section__catalog__textRight, .section__catalog__textLeft
    font-size: 21px
@media screen and (max-width: 900px)
  .section__catalog__buttonRedirectText
    font-size: 14px
  .section__catalog__textRight, .section__catalog__textLeft
    font-size: 18px
@media screen and (max-width: 700px)
  .section__catalog__buttonRedirectText
    font-size: 12px
  .section__catalog__textRight, .section__catalog__textLeft
    margin-top: 10%
    font-size: 15px



@media screen and (max-width: 1600px)
  .section__catalog__header
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__catalog__header
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__catalog__header
    font-size: 28px
@media screen and (max-width: 700px)
  .section__catalog__line
    margin-top: 6%
    margin-bottom: 5%



@media screen and (max-width: 800px)
  .section__catalog__buttonRow
    display: none
  .section__catalog__buttonRowHiddenWrap
    display: flex
@media screen and (max-width: 600px)
  .section__catalog__buttonRowHidden1
    padding: 0 6%
  .section__catalog__buttonRowHidden2
    padding: 0 6%
@media screen and (max-width: 600px)
  .section__catalog__buttonRowHidden1
    padding: 0 0
  .section__catalog__buttonRowHidden2
    padding: 0 0
@media screen and (max-width: 460px)
  .section__catalog__buttonRowHiddenWrap
    display: none
  .section__catalog__buttonRowHiddenWrap2
    display: flex
  .section__catalog__buttonRowHidden2
    justify-content: space-evenly

@media screen and (max-width: 750px)
  .section__catalog__items
    height: 400px
@media screen and (max-width: 400px)
  .section__catalog__items
    height: 350px
@media screen and (max-width: 400px)
  .section__catalog__items
    height: 350px