@import "src/styles-global/mixins"

.section
  &__review__wrap
    display: flex
    flex-direction: column
    padding-left: 4.2%
    padding-right: 4.2%
    margin-bottom: 4.2%
    margin-top: 4.2%
  &__review__header
    color: #000
    text-align: right
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__review__lineImg
    width: 100%
    margin-top: 2.3%
    margin-bottom: 0.8%
  &__review__gisImg
    display: flex
    justify-content: flex-start
    width: 14%
  &__review__button
    display: flex
    justify-content: center
    align-items: center
    padding: 0.683% 3.412%
    border-radius: 40px
    background: #525157
  &__review__buttonText
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__review__buttonWrap
    margin-top: 4%
    width: 100%
    display: flex
    align-items: center
    justify-content: center

  @media screen and (max-width: 1600px)
    &__review__buttonText
      font-size: 20px
  @media screen and (max-width: 1300px)
    &__review__buttonText
      font-size: 18px
    &__review__gisImg
      width: 17%
  @media screen and (max-width: 1100px)
    &__review__gisImg
      width: 19%
  @media screen and (max-width: 900px)
    &__review__gisImg
      width: 21%
  @media screen and (max-width: 800px)
    &__review__gisImg
      width: 23%
  @media screen and (max-width: 700px)
    &__review__gisImg
      width: 25%
    &__review__buttonWrap
      margin-top: 8%
      margin-bottom: 8%
    &__review__lineImg
      margin-top: 6%
      margin-bottom: 9%
  @media screen and (max-width: 500px)
    &__review__gisImg
      width: 28%
  @media screen and (max-width: 800px)
    &__review__button
      padding: 1% 4.412%

@media screen and (max-width: 1600px)
  .section__review__header
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__review__header
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__review__header
    font-size: 28px
