@import "src/styles-global/mixins"
@import "../header.module"


.btn
  padding: 0 !important

.header__button__phone
  padding: 3px 8px
a:focus, a:hover
  text-decoration: none
  color: white
.dropdown
  &__menu
    box-sizing: border-box
    position: relative
    min-width: 0
    width: 150%
    text-align: center
    background-color: #242526
    border: 1px solid rgba(255, 255, 255, 0.24)
  &__line
    margin-top: 2px
    margin-bottom: 7px
    color: rgba(255, 255, 255, 0.24)
    background-color: rgba(255, 255, 255, 0.24)
    border-top: 1px solid rgba(255, 255, 255, 0.24)
  &__text
    padding-bottom: 10px
    padding-top: 10px
    display: block
    color: white
    font-size: 14px
  &__text__default
    padding-bottom: 10px
    padding-top: 10px
    display: block
    color: white
    font-size: 14px
  &__text:hover
    color: white
    opacity: 0.8
  &__textCity
    display: block
    color: white
    font-size: 18px
  &__textCityButton:hover
    border-radius: 20px
    border: 1px solid white
    box-sizing: border-box
  &__textCityButton
    border: 1px solid #242526
    margin-top: 10px
    margin-bottom: 10px
    padding: 5px 10px
  &__textCityButton:active
    border-radius: 20px
    border: 1px solid white
  &__textQuestion
    padding-bottom: 10px
    padding-top: 10px
    display: block
    color: rgb(255, 255, 255)
    font-size: 18px
    cursor: pointer
    text-decoration: underline
  &__textQuestionHidden
    padding-bottom: 10px
    padding-top: 10px
    display: none
    color: rgb(255, 255, 255)
    font-size: 18px
    cursor: pointer
    text-decoration: underline
  &__textQuestion:hover, &__textQuestionHidden:hover
    opacity: 0.9
  &__city__row
    box-sizing: border-box
    display: flex
    flex-direction: row
    justify-content: center
    gap: 30px
    font-size: 16px
  &__textCityButton
    border-radius: 20px
    border: 1px solid white
    box-sizing: border-box

.dropdown__city__row>button
  border: 1px solid #242526
  margin-top: 10px
  margin-bottom: 10px
  padding: 5px 10px
  color: white
  font-size: 18px
.dropdown__city__row>button:hover
  border-radius: 20px
  border: 1px solid white
  box-sizing: border-box
.active
  border-radius: 20px
  border: 1px solid white
  box-sizing: border-box



@media screen and (max-width: 1000px)
  .dropdown__wrap
    display: block
  .dropdown__button
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    &__line
      width: 60%
      margin-top: 5%
      margin-bottom: 5%
      border-radius: 50px
      height: 1px
      background-color: white
@media screen and (max-width: 700px)
  .dropdown__textQuestionHidden
    display: block
  .dropdown__textQuestion
    display: none

@media screen and (max-width: 560px)
  .dropdown__wrap

@media screen and (max-width: 425px)
  .dropdown__button
    width: 35px
    height: 25px
  .dropdown__wrap
    margin-left: 0 !important
@media screen and (max-width: 400px)
  .dropdown-toggle
    padding: 0 !important
  .dropdown__wrap
    margin-left: 0 !important