.dropdown
  &__wrap
    margin-left: 10%
    display: none
  &__button
    display: none
    border-radius: 30px
    border: 1px solid white
    width: 50px
    height: 30px
    background-color: #242526
  &__menu
    min-width: 0
    width: 300%
    text-align: center
    background-color: #242526
    border: 1px solid rgba(255, 255, 255, 0.24)
  &__line
    margin-top: 2px
    margin-bottom: 7px
    color: rgba(255, 255, 255, 0.24)
    background-color: rgba(255, 255, 255, 0.24)
    border-top: 1px solid rgba(255, 255, 255, 0.24)
  &__text
    padding-bottom: 10px
    padding-top: 10px
    display: block
    color: white
    font-size: 14px
  &__text:hover
    color: white
    opacity: 0.8

@media screen and (max-width: 1000px)
  .dropdown__wrap
    display: block
  .dropdown__button
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    &__line
      width: 60%
      margin-top: 5%
      margin-bottom: 5%
      border-radius: 50px
      height: 1px
      background-color: white
@media screen and (max-width: 560px)
  .dropdown__wrap
    margin-left: 5% !important

@media screen and (max-width: 425px)
  .dropdown__button
    width: 35px
    height: 25px
  .dropdown__wrap
    margin-left: 0 !important
@media screen and (max-width: 390px)
  .dropdown-toggle
    padding: 0 !important
  .dropdown__wrap
    margin-left: 0 !important
@media screen and (max-width: 370px)
  .test
    padding-left: 0!important
@media screen and (max-width: 330px)
  .toggle
    padding-left: 0!important
    padding-right: 5px

