.section__order__galochka
  display: none

.section__order
  display: flex
  flex-direction: row
  justify-content: center
  &__galochka
    cursor: pointer
    position: absolute
    left: 1%
    bottom: -43%
  &__wrap
    overflow: hidden
    margin-top: 9%
    display: flex
    flex-direction: column
    margin-left: 4.2%
    margin-right: 4.2%
  &__headerWrap
    display: flex
    flex-direction: column
    width: 100%
  &__headerText
    color: #000
    text-align: right
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__lineImg
    margin-top: 3.25%
    margin-bottom: 7.2%
  &__elem1
    display: flex
    position: relative
    align-items: center
    justify-content: flex-start
    flex-direction: row
    border-radius: 20px
    background: #0047FF
    width: 30%
    min-width: 0
    min-height: 0
    flex-shrink: 0
  &__elem1Text
    width: 100%
    position: absolute
    display: flex
    flex-direction: column
    transform: rotate(90deg)
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 46px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__elem1TextWrap
    bottom: 55%
    left: -48%
    width: 110%
    position: absolute
    display: flex
    justify-content: center
  &__elem2
    display: flex
    flex-direction: column
    width: 20%
    text-align: center
    margin-left: 5%
    margin-right: 5%
  &__elem3
    width: 30%
    display: flex
    flex-direction: row
  &__formFields
    position: relative
    width: 100%
    padding-right: 7%
    display: flex
    flex-direction: column
  &__elem2Text1
    margin-top: 7.1%
    color: #2F2F2F
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__elem2Text2
    color: #000
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__fieldWrap
    width: 100%
    display: flex
    justify-content: flex-end
  &__field
    display: flex
    align-items: center
    justify-content: center
    border-radius: 10px
    background: #FFF
    width: 82%
    height: 50px
    flex-shrink: 0
  &__fieldText:nth-child(1n)
    margin-top: 10%
  &__fieldText
    display: flex
    justify-content: flex-end
    margin-bottom: 4%
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 700
    line-height: normal
  &__termsBlock
    position: relative
    display: flex
    justify-content: flex-end
    align-items: center
    margin-left: 20%
    flex-direction: row
  &__buttonWrap
    width: 100%
    display: flex
    justify-content: flex-end
    align-items: flex-start
  &__button
    margin-top: 5%
    margin-bottom: 5%
    display: flex
    align-items: center
    justify-content: center
    border-radius: 30px
    border: 1px solid #989595
    background: #525157
    width: 150px
    height: 40px
    flex-shrink: 0
  &__button:hover
    opacity: 0.8
  &__buttonText
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 700
    line-height: normal
  &__text
    display: flex
    justify-content: flex-end
    color: #d2d0d0
    text-align: right
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__text:hover
    color: white
  &__textDefault
    margin-top: 10%
    color: #ffffff
    text-align: right
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__buttonTerms
    margin-top: 10%
    margin-left: 15%
  &__innerInput
    color: #1f1f1f
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 800
    line-height: normal
  &__cardImgWrap
    display: flex
    align-items: center
  &__cardImg
    background: url(../../assets/woman__filtered__2.png) no-repeat
    width: 21rem
    height: 547px
    border-radius: 27px
    flex-shrink: 0
    display: flex
    flex-direction: column
    position: relative
  &__socialsGroup
    margin-left: 6%
    display: flex
    justify-content: space-between
    flex-direction: column
  &__socialsWU, &__socialsVK, &__socialsTG, &__socialsPhone
    display: flex
    align-items: center
    justify-content: center
    border-radius: 20px
    width: 100px
    height: 100px
  &__socialsWU
    background: #698F72
  &__socialsWU:hover
    opacity: 0.9
  &__socialsVK
    background: #0047FF
  &__socialsVK:hover
    opacity: 0.9
  &__socialsTG
    background: #658BAD
  &__socialsTG:hover
    opacity: 0.9
  &__socialsPhone
    background: #525157
  &__socialsPhone:hover
    opacity: 0.9
  &__ImgText1, &__ImgText3
    z-index: 4
    margin-left: 5%
    margin-top: 80%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 32px
    font-style: normal
    font-weight: 600
    line-height: 40px
  &__ImgText3
    margin-top: 5%
  &__ImgText2, &__ImgText4
    z-index: 4
    margin-left: 5%
    margin-top: 3%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 600
    line-height: 30px

@media screen and (min-width: 700px)
  .section__order__phoneA
    pointer-events: none

.line__order
  margin-top: 7.1%
  margin-bottom: 7.1%

.black__logo__order
  min-width: 0
  min-height: 0

.black__logo__orderWrap
  display: flex
  align-items: center
  justify-content: center
.section__order__termsBlock>input
  left: 0
  bottom: -50%
  width: 25px
  height: 25px
  border-radius: 5px
  display: block
  position: absolute
  border: 2px solid #ffffff
  background-color: #ffffff
  cursor: pointer

