      margin-right: 3%
@media screen and (max-width: 1600px)
  .section__order__elem3
    margin-right: 5%
@media screen and (max-width: 1399px)
  .section__order__elem3
    margin-right: 3%
  .section__order
    justify-content: center

  .section__order__elem2
    display: none
  .section__order__elem1
    width: auto
    margin-right: 10%
@media screen and (max-width: 1399px)
  .section__order__elem3
    width: auto
    margin-left: 10%
  .section__order__elem1
    margin-right: 0
@media screen and (max-width: 1100px)
  .section__order__elem3
    margin-left: 6%
@media screen and (max-width: 1023px)
  .section__order__cardImg
    display: none
  .section__order__elem3
    margin-left: 3.5%
    margin-right: 0
  .section__order__socialsGroup
    margin-left: 0
@media screen and (max-width: 600px)
  .section__order
    flex-direction: column
    padding-left: 5%
    padding-right: 5%
    width: 100%
    justify-content: center
    &__elem3
      margin-top: 5%
      margin-left: 0
    &__socialsGroup
      margin-left: 0
      flex-direction: row
      justify-content: space-between
      width: 100%
@media screen and (max-width: 550px)
