@media screen and (max-width: 320px)
  .section__order
    &__socialsWU, &__socialsVK, &__socialsTG, &__socialsPhone
      border-radius: 15px
      width: 55px
      height: 55px

@media screen and (max-width: 700px)
  .section__order__lineImg
    margin-bottom: 12%
    margin-top: 7%
  .section__order
    padding-bottom: 10%