@import "src/styles-global/mixins"

.section__footer
  display: flex
  flex-direction: column
  justify-content: space-around
  align-items: center
  width: 70%
  margin-top: 30px
  &__wrap
    height: 170px
    align-items: center
    width: 100%
    flex-shrink: 0
    background-color: #242526
    display: flex
    flex-direction: row
    justify-content: space-around
  &__column1, &__column1Hidden
    width: 100%
    display: flex
    flex-direction: row
    justify-content: space-around
    align-items: center
  &__column1Hidden
    flex-direction: column
    display: none
  &__hiddenRow1
    display: flex
    flex-direction: row
    width: 70%
    justify-content: space-between
  &__hiddenRow2
    display: flex
    flex-direction: row
    width: 60%
    justify-content: space-between
  &__column2
    width: 100%
    display: flex
    justify-content: center
    flex-direction: row
    align-items: center
  &__text
    cursor: pointer
    color: #FFFFFFCE
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__textDisabled
    color: #FFFFFFCE
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__text2
    color: #FFFFFFCE
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__text:hover
    color: white
.line
  height: 2px
  width: 85%
  margin-bottom: 20px
  margin-top: 20px
  color: white

@media screen and (max-width: 1300px)
  .section__footer
    width: 80%

@media screen and (max-width: 1100px)
  .section__footer__text, .section__footer__text2, .section__footer__textDisabled
    font-size: 18px
  .section__footer__wrap

@media screen and (max-width: 950px)
  .section__footer
    width: 98%

@media screen and (max-width: 850px)
  .section__footer__text, .section__footer__text2, .section__footer__textDisabled
    font-size: 16px
@media screen and (max-width: 700px)
  .section__footer
    margin-top: 0
  .section__footer__wrap
    height: 200px
  .section__footer__column1
    display: none
  .section__footer__column1Hidden
    display: flex
@media screen and (max-width: 560px)
  .section__footer__hiddenRow1
    width: 80%
  .section__footer__hiddenRow2
    width: 70%
@media screen and (max-width: 490px)
  .section__footer__hiddenRow1
    width: 85%
  .section__footer__hiddenRow2
    width: 75%
@media screen and (max-width: 440px)
  .section__footer__hiddenRow1
    width: 85%
  .section__footer__hiddenRow2
    width: 85%
@media screen and (max-width: 390px)
  .section__footer__text, .section__footer__text2, .section__footer__textDisabled
    font-size: 14px
@media screen and (max-width: 340px)
  .section__footer__text, .section__footer__text2, .section__footer__textDisabled
    font-size: 13px