@import "src/styles-global/mixins"

.comment
  border-radius: 30px
  background: #0047FF
  display: flex
  flex-direction: column
  align-items: flex-start
  position: relative
  &__text
    width: 90%
    padding-left: 5%
    padding-top: 3.35%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__starImgArray
    display: flex
    flex-direction: row
    margin-right: 5%
    margin-bottom: 2%
  &__starImgArrayWrap
    width: 100%
    display: flex
    justify-content: flex-end
.star
  min-width: 0
  min-height: 0
@media screen and (max-width: 1300px)
  .comment__starImgArray
    margin-bottom: 5%
@media screen and (max-width: 800px)
  .comment
    border-radius: 20px
  .comment__starImgArrayWrap
    margin-top: 3%
@media screen and (max-width: 700px)
  .comment__text
    padding-top: 6%
    padding-right: 6%
    padding-left: 6%
@media screen and (max-width: 600px)
  .comment__text
    font-size: 18px
@media screen and (max-width: 532px)
  .comment
    border-radius: 20px
  .comment__text
    width: 100%
    font-size: 16px
  .star
    width: 1rem
@media screen and (max-width: 450px)
  .comment
    border-radius: 12px
  .comment__text
    font-size: 14px
  .star
    width: 0.8rem
@media screen and (max-width: 396px)
  .comment__text
    font-size: 13px
@media screen and (max-width: 340px)
  .comment__text
    font-size: 12px



