@import "src/styles-global/mixins"

.card
  &__wrap
    display: flex
    flex-direction: column
    border-radius: 30px
    border: 1px solid #a4a3a3
    width: 30%
    position: relative
    justify-content: center
    background-color: white
  &__link
    position: absolute
    width: 100%
    height: 100%
    cursor: pointer
    z-index: 10
    opacity: 0
  &__img
    padding-left: 5%
    padding-right: 5%
    padding-top: 5%
  &__text__wrap
    padding-top: 3%
    padding-bottom: 3%
    display: flex
    width: 100%
    flex-direction: row
    align-items: center
    justify-content: space-between
    padding-left: 7%
    padding-right: 7%
  &__text__name, &__text__price
    font-size: 24px
    font-family: Inter, sans-serif
    font-weight: 300

@media screen and (max-width: 1300px)
  .card
    &__wrap
      border-radius: 20px
@media screen and (max-width: 1070px)
  .card
    &__text__name, &__text__price
      font-size: 22px
@media screen and (max-width: 1000px)
  .card
    &__text__name, &__text__price
      font-size: 20px
@media screen and (max-width: 1000px)
  .card
    &__text__wrap
      flex-direction: column
    &__text__name
      margin-bottom: 5%
@media screen and (max-width: 750px)
  .card
    &__text__name, &__text__price
      font-size: 18px
@media screen and (max-width: 600px)
  .card
    &__text__name, &__text__price
      font-size: 16px
    &__text__name
      margin-bottom: 8%
@media screen and (max-width: 500px)
  .card
    &__text__name, &__text__price
      font-size: 14px
@media screen and (max-width: 450px)
  .card
    &__text__name, &__text__price
      font-size: 13px
@media screen and (max-width: 400px)
  .card
    &__text__name, &__text__price
      font-size: 12px
@media screen and (max-width: 370px)
  .card
    &__text__name, &__text__price
      font-size: 11px
@media screen and (max-width: 345px)
  .card
    &__text__name, &__text__price
      font-size: 10px
@media screen and (max-width: 900px)
  .card
    &__wrap
      border-radius: 15px
@media screen and (max-width: 700px)
  .card
    &__wrap
      border-radius: 13px
@media screen and (max-width: 550px)
  .card
    &__wrap
      border-radius: 11px
@media screen and (max-width: 400px)
  .card
    &__wrap
      border-radius: 9px