@import "src/styles-global/mixins"

@media screen and (min-width: 852px)
  .section__map__column2Button:nth-child(3)
    display: none
  .section__map__column2Button:nth-child(4)
    margin-top: 5%
    display: none
@media screen and (max-width: 852px)
  .section__map__column2Button:nth-child(2)
    display: none
  .section__map__column2Button:nth-child(3)
    margin-top: 5%
    margin-bottom: 5%
    margin-left: 5%
    display: none
  .section__map__column2Button:nth-child(2)
    margin-top: 5%
    margin-bottom: 5%
    margin-right: 5%
    display: none


.section__map
  &__wrap
    display: flex
    flex-direction: column
    justify-content: center
    padding-left: 4.2%
    padding-right: 4.2%
  &__heaaderWrap
    width: 100%
  &__header
    text-align: right
    color: #000
    display: flex
    justify-content: flex-end
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__lineImg
    margin-top: 2.1%
    width: 100%
  &__mapWrap
    margin-top: 6.5%
    margin-bottom: 6.5%
    display: flex
    align-items: center
    justify-content: center
  &__yandexMap
    border: 2px solid black
    width: 1000px
    height: 1000px

  &__yandexMapWrap, &__yandexMapWrapHidden
    border: 2px solid black
    margin-top: 5%
    margin-bottom: 5%
    display: flex
    align-items: center
    width: 60%
    justify-content: space-between
  &__rowWrap
    display: flex
    flex-direction: row
  &__column2
    padding-top: 2%
    padding-bottom: 2%
    border-radius: 100px
    background-color: white
    margin: 6% 3% 20% 7%
    display: flex
    flex-direction: column
    align-items: center
  &__rectangle
    border-radius: 435px
    border-top-left-radius: 50px !important
    margin-top: 2%
    z-index: -1
    position: absolute
    width: 60rem
    height: 60rem
    background-color: rgba(0, 71, 255, 0.97)
  &__columnHeader
    color: #000
    display: flex
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__columnHeaderLine
    margin-top: 5%
    margin-bottom: 5%
    width: 40%
  &__columnHeaderWrap
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  &__column2Button
    font-size: 22px
    font-family: Inter, sans-serif
    display: flex
    background-color: #fcfcff
    flex-direction: column
    align-items: center
    border: 3px solid rgba(180, 177, 177, 0.32)
    box-shadow: rgba(180, 177, 177, 0.32)
    width: 55%
    padding: 10px 10px
    border-radius: 20px
  &__column2Button:hover
    opacity: 0.8
    border: 3px solid #0047ff
  &__smallLine
    padding-top: 3%
    width: 100%
    padding-bottom: 2%
  &__column2Hidden
    display: none
    flex-direction: column
    align-items: center
  &__hiddenButtonRow
    display: flex
    flex-direction: row
    justify-content: center





@media screen and (max-width: 1600px)
  .section__map__header
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__map__header
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__map__header
    font-size: 28px
@media screen and (max-width: 850px)
  .section__map__columnHeader
    font-size: 30px
@media screen and (max-width: 700px)
  .section__map__columnHeader
    font-size: 25px
@media screen and (max-width: 850px)
  .section__map__lineImg
    margin-top: 4%
    margin-bottom: 3%
  .section__map__yandexMapWrap
    display: none
@media screen and (max-width: 700px)
  .section__map__lineImg
    margin-top: 6%
    margin-bottom: 5%
@media screen and (max-width: 1500px)
  .section__map__rectangle
    width: 50rem
    height: 50rem
@media screen and (max-width: 1300px)
  .section__map__rectangle
    width: 45rem
    height: 45rem
@media screen and (max-width: 1100px)
  .section__map__column2
    padding-top: 8%
    padding-bottom: 8%
  .section__map__column2Button
    width: 82%
    padding: 10px 30px !important
@media screen and (min-width: 851px)
  .section__map__yandexMapWrapHidden
    display: none

@media screen and (max-width: 850px)
  .section__map__yandexMapWrapHidden
  .section__map__hiddenButtonRow

  .section__map__column2Button
    padding: 5px 5px !important
    width: 28%
    font-size: 20px
    display: flex
    align-items: center
    justify-content: center
  .section__map__column2Hidden
    display: flex
  .section__map__rectangle, .section__map__column2
    display: none
  .section__map__rowWrap
    align-items: center
  .section__map__yandexMapWrap, .section__map__yandexMapWrapHidden
    width: 80%
  .section__map__rowWrap
   flex-direction: column
  .section__map__column2
    padding-top: 12%
    padding-bottom: 12%
@media screen and (max-width: 660px)
  .section__map__column2Button
    width: 30%
    font-size: 18px
@media screen and (max-width: 520px)
  .section__map__column2Button
    width: 31%
    font-size: 15px
@media screen and (max-width: 420px)
  .section__map__column2Button
    width: 31%
    font-size: 13px

@media screen and (max-width: 900px)
  .section__map__rectangle
    width: 40rem
    height: 45rem
@media screen and (max-width: 750px)
  .section__map__rectangle
    width: 35rem
    height: 45rem
@media screen and (max-width: 600px)
  .section__map__rectangle
    width: 20rem
    height: 45rem

