@import "Fonts"
@import "Colors"

/* W3C standard
      Firefox */
*
  scrollbar-width: thin
  scrollbar-color: #0047FF #f3f0f0


/*  Chrome/Edge/Safari */
*::-webkit-scrollbar
  width: 10px

*::-webkit-scrollbar-track
  background: #f3f0f0

*::-webkit-scrollbar-thumb
  background-color: #0047FF
  border-radius: 10px

