@import "src/styles-global/mixins"

.section
  &__wrap__about
    display: flex
    flex-direction: column
    margin-top: 5%
    margin-left: 4.2%
    margin-right: 4.2%
  &__text__about
    color: black
    justify-content: flex-end
    display: flex
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__line
    &__img
      width: 100%
      margin-bottom: 2.275%
      margin-top: 2.275%
  &__line3
    width: 100%
    margin-top: 1.71%
    margin-bottom: 1.71%
.column
  &__elem__1
    justify-content: space-between
    display: flex
    flex-direction: row
  &__elem__2
    width: 100%
    margin-top: 3.45%
    display: flex
    align-items: center
    justify-content: center
    flex-direction: row
  &__elem__3
    margin-top: 4.21%
    display: flex
    width: 100%
    align-items: center
    flex-direction: column
    justify-content: center
  &__inner__1, &__inner__2
    display: flex
    flex-direction: column
  &__inner__1
    justify-content: space-between
    width: 48%
  &__inner__2
    justify-content: space-between
    width: 48%
    margin-left: 3.5%
  &__header__textColumn1, &__header__textColumn2,
  &__header__textCenter
    color: $colorGreyAboutText
    display: block
    width: 100%
    font-family: Inter, sans-serif
    font-size: 36px
    font-style: normal
    font-weight: 300
    line-height: normal
  &__header__textColumn2
    text-align: right
  &__header__textCenter
    text-align: center

  &__default__textColumn1, &__default__textColumn2,
  &__default__textColumn3
    display: block
    color: $colorGreyAboutText
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 300
    line-height: normal
  &__default__textColumn3
    text-align: center
    margin-bottom: 4%
  &__default__textColumn2
    text-align: right
  &__small__line
    width: 88%
    margin-top: 2.4%
    margin-bottom: 2.4%
  &__small__lineWrap
    display: flex
    justify-content: flex-end

.innerColumn__text
  &__block1, &__block3
    margin-top: 7.9%
    margin-bottom: 7.9%
  &__block2
    margin-bottom: 4.7%

.test
  min-width: 0

.woman__with__dog
  min-width: 0
  min-height: 0

.woman
  width: 100%

.man__blue, .man__grey
  min-width: 0
  min-height: 0

.woman__white__img
  margin-left: 3.542%
  margin-right: 3.542%
  min-width: 0
  min-height: 0

