@import "src/styles-global/mixins"

.header
  background: $colorBlackNav
  display: flex
  flex-direction: row
  align-items: center
  justify-content: space-around
  padding-left: 2%
  box-sizing: border-box
  &__button__link
    display: flex
    flex-direction: row
    cursor: pointer
    align-items: center


  &__wrap
    z-index: 20
    width: 100%
    position: sticky
    position: -webkit-sticky
    top: 0
  &__logo__vv
    color: $colorWhite
    font-size: 40px
    font-family: Cormorant SC, sans-serif
    font-style: normal
    font-weight: 300
    padding-top: 1.5%
    padding-bottom: 1.5%
  &__icon__map
    margin-left: 10%
    margin-right: 5%
    width: 10.3%
    flex-shrink: 0
  &__img__line

  &__navText
    &__shop

    &__catalog, &__review, &__order

    &__shop, &__about, &__catalog, &__review, &__order
      color: #FFF
      font-size: 24px
      font-family: Inter, sans-serif
      font-style: normal
      font-weight: 200
    &__textShop
      margin-left: 0.521% !important
  &__button__phone

  &__button__city

  &__button__phone, &__button__city, &__button__order
    display: flex
    align-items: center
    justify-content: center
    padding: 0.365% 1.042%
    border-radius: 30px
    border: 1px solid $colorWhite
    background: $colorBlackNav

  &__buttonText__phone, &__buttonText__city, &__buttonText__order
    color: $colorWhite
    font-size: 24px
    font-family: Inter, sans-serif
    font-style: normal
    line-height: normal
  &__buttonText__phone
    padding: 0
  &__buttonText__order
    font-size: 13px
  &__buttonText__phone
      font-weight: 600
  &__buttonText__city
      font-weight: 300
  &__navText__shop,
  &__navText__about,
  &__navText__catalog,
  &__navText__review,
  &__navText__order
    color: rgba(255, 255, 255, 0.89)
    position: relative
  &__navText__shop:hover,
  &__navText__about:hover,
  &__navText__catalog:hover,
  &__navText__review:hover,
  &__navText__order:hover
    box-sizing: border-box
    color: white
  &__navText__shop::before,
  &__navText__about::before,
  &__navText__catalog::before,
  &__navText__review::before,
  &__navText__order::before
    content: ""
    position: absolute
    bottom: 0
    left: 0
    width: 0
    margin-bottom: -0.5rem
    height: 3px
    background-color: #1558fd
    transition: width 0.2s ease-in
  &__navText__shop:hover::before,
  &__navText__about:hover::before,
  &__navText__catalog:hover::before,
  &__navText__review:hover::before,
  &__navText__order:hover::before
    width: 100%
.logo__hidden
  display: none



