@import "src/styles-global/mixins"

.background__img
  animation: fade 3s linear infinite

@keyframes fade
  0%
    opacity: 0
  10%
    opacity: 0
  50%
    opacity: 1
  90%
    opacity: 0
  100%
    opacity: 0

.background
  position: absolute
  width: 60%
  right: 0
  height: 100%
  background-color: #ececec
  z-index: -1
  &__img
    position: absolute
    right: 0
    height: 100%
.section__start
  position: relative
  width: 100%
  &__header
    font-family: Cormorant SC, Inter, sans-serif
    font-weight: 300
    font-size: 60px
  &__row1
    padding-top: 5%
    margin-left: 15%
    display: flex
    flex-direction: row
  &__row2
    margin-top: 5%
    margin-left: 10%
    display: flex
    flex-direction: row
  &__row3
    margin-top: 2%
    padding-bottom: 5%
  &__textBlock
    margin-left: 15%
    display: flex
    flex-direction: column
    font-family: Inter, sans-serif
    font-weight: 600
  &__text2:nth-child(2)
    margin-top: 5%
    margin-bottom: 5%
  &__text1
    font-size: 22px
  &__text2
    font-size: 16px
    color: #0047ff
  &__text3
    font-size: 20px
    text-decoration: underline
    color: black
    font-weight: 500
    margin-left: 8%
  &__text3:hover
    opacity: 0.8
    cursor: pointer
  &__text2:hover
    opacity: 0.7
    cursor: pointer
  &__imgWrap
    display: flex
    flex-direction: column
  &__imgWrap>img:nth-child(2n)
    margin-top: 100%
    margin-bottom: 100%
  &__img
    cursor: pointer
  &__img:hover
    opacity: 0.7
  &__sizeWrap
    position: relative
    margin-top: 4%
    display: flex
    flex-direction: column
  &__sizeWrap:after
    content: url("../../assets/plus.png")
    position: absolute
    right: -27%
    top: -20%
  &__sizeText1, &__sizeText2
    font-family: 'Libre Baskerville', sans-serif
    margin-left: 30%
    font-size: 160px
  &__sizeText2
    color: #ff4040
    text-indent: 10%
  &__smallLine
    width: 20%
    height: 3px
    margin-left: 15%
  &__imgWrap:nth-child(2n)
    margin-top: 70%
    margin-bottom: 70%

@media screen and (max-width: 1560px)
  .section__start
    &__row1
      margin-left: 8%
    &__textBlock
      margin-left: 10%
@media screen and (max-width: 1280px)
  .section__start
    &__row1
      margin-left: 6%
    &__textBlock
      margin-left: 8%
@media screen and (max-width: 1200px)
  .section__start
    &__row1
      margin-left: 6%
    &__textBlock
      margin-left: 3%
      text-align: right
    &__sizeText1, &__sizeText2
      margin-left: 25%
      font-size: 130px
@media screen and (max-width: 1060px)
  .section__start
    &__header
      width: 12%
    &__textBlock
      margin-left: 5%
      text-align: right
    &__smallLine
      width: 20%
      height: 3px
      margin-left: 10%
    &__sizeText1, &__sizeText2
      margin-left: 25%
      font-size: 110px
@media screen and (max-width: 1020px)
  .background
    filter: blur(2px)
    &__img
      opacity: 0.8
  .section__start
    &__smallLine
      display: none
    &__row3
      margin-top: 8%
      padding-bottom: 2%
    &__textBlock
      margin-left: 10%
      text-align: right
  @media screen and (max-width: 940px)
    .background
      filter: blur(2px)
      &__img
        opacity: 0.7
    .section__start
      &__sizeText1, &__sizeText2
        margin-left: 20%
        font-size: 100px
        text-align: right
      &__sizeWrap
        margin-top: 10%
      &__sizeWrap:after
        content: url("../../assets/plus.png")
        position: absolute
        right: -27%
        top: -30%
  @media screen and (max-width: 883px)
    .section__start
      &__textBlock
        margin-left: 58%
        text-align: right
      .background
        filter: blur(2px)
        &__img
          opacity: 0.5
  @media screen and (max-width: 800px)
    .section__start
      &__textBlock
        margin-left: 50%
        text-align: right
      &__row2
        margin-top: 15%
        margin-bottom: 15%
  @media screen and (max-width: 650px)
    .section__start
      &__textBlock
        margin-left: 45%
        text-align: right

  @media screen and (max-width: 565px)
    .section__start
      &__textBlock
        margin-left: 40%
        text-align: right
  @media screen and (max-width: 500px)
    .section__start

      &__textBlock
        margin-left: 35%
        text-align: right

  @media screen and (max-width: 467px)
    .section__start
      &__header
        font-size: 35px
      &__row1
        margin-left: 2%
      &__textBlock
        margin-left: 30%
        text-align: right
  @media screen and (max-width: 420px)
    .section__start
      &__textBlock
        text-align: right
      &__sizeText1, &__sizeText2
        margin-left: 30%
        font-size: 80px
        text-align: right
      &__sizeWrap:after
        content: url("../../assets/plus.png")
        position: absolute
        right: -40%
        top: -30%
  @media screen and (max-width: 375px)
    .section__start
      &__text1
        font-size: 18px
      &__text2, &__text3
        font-size: 15px
      &__sizeWrap
        margin-top: 10%
      &__sizeText1, &__sizeText2
        margin-left: 20%
        margin-top: 10%
        font-size: 70px
        text-align: right
      &__sizeWrap:after
        content: url("../../assets/plus.png")
        position: absolute
        right: -35%
        top: -20%
  @media screen and (max-width: 355px)
    .section__start
      &__text1
        font-size: 15px
      &__text2, &__text3
        font-size: 12px
      &__textBlock
        margin-left: 30%
        text-align: right